//
import item from '../../children/item'
let echarts = require('echarts/lib/echarts')
    //
require('echarts/lib/chart/bar')
require("echarts/lib/chart/lines")
require('echarts/lib/chart/pie')
require("echarts/lib/component/toolbox")
require('echarts/lib/component/title')
export default {
    name: 'findProject',
    data() {
        return {
            carouselImgUrl: [
                require('../../../assets/2.jpg'),
                require('../../../assets/2.jpg'),
                require('../../../assets/2.jpg'),
                require('../../../assets/2.jpg'),
                require('../../../assets/2.jpg'),
                require('../../../assets/2.jpg')
            ],
            moneyTop: [{
                imgUrl: require('../../../assets/2.jpg'),
                header: '',
                authorHeadUrl: require('../../../assets/head.jpeg'),
                authorName: 'tianyu',
                description: ' 30 ',
                money: 220657.46,
                progress: 40,
                followerNum: 1773,
                commentNum: 157
            }, {
                imgUrl: require('../../../assets/2.jpg'),
                header: '',
                authorHeadUrl: require('../../../assets/head.jpeg'),
                authorName: 'tianyu',
                description: ' 30 ',
                money: 220657.46,
                progress: 40,
                followerNum: 1773,
                commentNum: 157
            }, {
                imgUrl: require('../../../assets/2.jpg'),
                header: '',
                authorHeadUrl: require('../../../assets/head.jpeg'),
                authorName: 'tianyu',
                description: ' 30 ',
                money: 220657.46,
                progress: 40,
                followerNum: 1773,
                commentNum: 157
            }, {
                imgUrl: require('../../../assets/2.jpg'),
                header: '',
                authorHeadUrl: require('../../../assets/head.jpeg'),
                authorName: 'tianyu',
                description: ' 30 ',
                money: 220657.46,
                progress: 40,
                followerNum: 1773,
                commentNum: 157
            }, {
                imgUrl: require('../../../assets/2.jpg'),
                header: '',
                authorHeadUrl: require('../../../assets/head.jpeg'),
                authorName: 'tianyu',
                description: ' 30 ',
                money: 220657.46,
                progress: 40,
                followerNum: 1773,
                commentNum: 157
            }],
            activeName: '1',
            myChart: {},
            myChartPie: {},
        };
    },
    beforeCreate () {
      this.$axios.get('')  
    },
    mounted() {
        let echarts = this.drawChart()
        this.myChart = echarts.myChart
        this.myChartPie = echarts.myChartPie
        window.onresize = () => {
            this.myChart.resize()
            this.myChartPie.resize()
        }
    },
    methods: {
        drawChart() {
            let idx = 1
            let myChart = echarts.init(document.getElementById('myChart'))
            let myChartPie = echarts.init(document.getElementById('myChartPie'))
            myChart.setOption({
                title: {
                    x: 'center',
                    text: 'ECharts',
                    subtext: 'Rainbow bar example',
                    link: 'http://echarts.baidu.com/doc/example.html'
                },
                tooltip: {
                    trigger: 'item'
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataView: { show: true, readOnly: false },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                calculable: true,
                grid: {
                    borderWidth: 0,
                    y: 80,
                    y2: 60
                },
                xAxis: [{
                    type: 'category',
                    show: false,
                    data: ['Line', 'Bar', 'Scatter', 'K', 'Pie', 'Radar', 'Chord', 'Force', 'Map', 'Gauge', 'Funnel']
                }],
                yAxis: [{
                    type: 'value',
                    show: false
                }],
                series: [{
                    name: 'ECharts',
                    type: 'bar',
                    itemStyle: {
                        normal: {
                            color: function(params) {
                                // build a color map as your need.
                                var colorList = [
                                    '#C1232B', '#B5C334', '#FCCE10', '#E87C25', '#27727B',
                                    '#FE8463', '#9BCA63', '#FAD860', '#F3A43B', '#60C0DD',
                                    '#D7504B', '#C6E579', '#F4E001', '#F0805A', '#26C0C0'
                                ];
                                return colorList[params.dataIndex]
                            },
                            label: {
                                show: true,
                                position: 'top',
                                formatter: '{b}\n{c}'
                            }
                        }
                    },
                    data: [12, 21, 10, 4, 12, 5, 6, 5, 25, 23, 7],
                    markPoint: {
                        tooltip: {
                            trigger: 'item',
                            backgroundColor: 'rgba(0,0,0,0)',
                            formatter: function(params) {
                                return '<img src="' +
                                    params.data.symbol.replace('image://', '') +
                                    '"/>';
                            }
                        },
                        data: [
                            { xAxis: 0, y: 350, name: 'Line', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 1, y: 350, name: 'Bar', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 2, y: 350, name: 'Scatter', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 3, y: 350, name: 'K', symbolSize: 20, symbol: 'image://../asset/ico/K.png' },
                            { xAxis: 4, y: 350, name: 'Pie', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 5, y: 350, name: 'Radar', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 6, y: 350, name: 'Chord', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 7, y: 350, name: 'Force', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 8, y: 350, name: 'Map', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 9, y: 350, name: 'Gauge', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                            { xAxis: 10, y: 350, name: 'Funnel', symbolSize: 20, symbol: 'image://../asset/ico/.png' },
                        ]
                    }
                }]
            })
            myChartPie.setOption({
                title: {
                    text: '',
                    subtext: '',
                    x: 'center'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: 'vertical',
                    x: 'left',
                    data: ['', '', '', '', '']
                },
                toolbox: {
                    show: true,
                    feature: {
                        mark: { show: true },
                        dataView: { show: true, readOnly: false },
                        magicType: {
                            show: true,
                            type: ['pie', 'funnel'],
                            option: {
                                funnel: {
                                    x: '25%',
                                    width: '50%',
                                    funnelAlign: 'left',
                                    max: 1548
                                }
                            }
                        },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                calculable: true,
                series: [{
                    name: '',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '60%'],
                    data: [
                        { value: 335, name: '' },
                        { value: 310, name: '' },
                        { value: 234, name: '' },
                        { value: 135, name: '' },
                        { value: 1548, name: '' }
                    ]
                }]
            })
            return { myChart: myChart, myChartPie: myChartPie }
        }
    },
    components: {
        item
    }
}


